 }
h1:before {
  content: "Hello! I am Kristin";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: white;
  overflow: hidden;
  animation: animate 6s infinite;
}
@keyframes animate {
  0%{
      height: 25%;
  }
  25%{
      height: 50%;
  }
  50%{
      height: 65%;
  }
  75%{
      height: 40%;
  }
  100%{
      heigth: 25%;
  }
}
